.grid {
    display: grid;
    grid-template-columns: repeat(8, 100px); 
    grid-gap: 0px
    
}

.SschessContainer{
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    padding-right: 3%;
    border-left: 2px solid var(--headingBG); /* Borde de la caja */
}
.gridContainer {
    display: flex;
    flex-direction: column;
    justify-content: right;
    align-items: right;
}

.tituloSSchess {
    font-size: 3rem;
    text-align: center;
}

.nivelActual {
    font-size:x-large;
    align-self: center; 

}

.buttonContainer {
    align-self: center; 
}

.gameInfoContainer {
    color: white;
    width: 10rem;
    height: 40rem;
    display: flex;
    gap: 15rem;
    flex-direction: column;
}

.mensajeSecretoContainer {
    height: 30rem;
    border: 2px solid #000; /* Borde de la caja */
    padding: 20px; /* Espaciado interno */
    align-self:last baseline;
}
button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: #25C2A0;
    color: black;
    border: none;
    border-radius: 5px;
    align-self: left;
    cursor: pointer;
}
.cell {
    width: 100px;
    height: 100px;
    background-color: var(--gridColor);
    border: 0px solid var(--gridBorders);
  }